import React from 'react';
import PropTypes from 'prop-types';
import classNames from 'classnames';
import { makeIconFromProp, makeCallback, emptyChildren, getLastChild, BaseProps, Size, Color } from '../../../shared';
import Icon from '../Icon';
import Spinner from '../Spinner';

import './button.scss';

/**
 * A Button component is used to indicates a user's action
 */
const Button = (props) => {
  const {
    children, type, fluid, color, rounded, outline, disabled, size, icon, hover, spinner, onClick, className, id, style,
  } = props;

  const lastChild = getLastChild(children);

  const classList = classNames('bi bi-btn', `btn-${color}`, {
    'btn-fluid': fluid,
    'btn-outline': outline,
    'btn-rounded': rounded,
    'btn-he-zoom': hover === 'zoom',
    'btn-he-float': hover === 'float' || hover === true,
    'btn-he-shrink': hover === 'shrink',
    'btn-he-refl': hover === 'reflection',
    'btn-he-rnd': hover === 'round',
    'btn-sm': size === 'small',
    'btn-lg': size === 'large',
    'icon-only': emptyChildren(children),
    'icon-right': lastChild && lastChild.type && lastChild.type.type.name === 'Icon',
  }, className);

  console.log('SPINNER PROP', spinner);

  return (
    /* eslint-disable-next-line react/button-has-type */
    <button id={id} disabled={disabled} type={type} onClick={makeCallback(onClick)} className={classList} style={style}>
      {/* Generate icon if exists */}
      {!!icon && makeIconFromProp(icon)}
      {children}
    </button>
  );
};

Button.propTypes = {
  ...BaseProps,
  /**
   * define the button's color, can be `default`, `primary`, `secondary`, `info`, `warning`, `success`, `error
   * or `transparent`
   * @default "default"
   */
  color: Color,
  /**
   * define the button's size, can be `small`, `default`, `large`
   * @default "default"
   */
  size: Size,
  /**
   * shows the outlines only
   * @default false
   */
  outline: PropTypes.bool,
  /**
   * makes the button rounded
   * @default false
   */
  rounded: PropTypes.bool,
  /**
   * defines the button's type
   * @default 'button'
   */
  type: PropTypes.oneOf(['submit', 'button', 'reset']),
  /**
   * disables the button
   * @default false
   */
  disabled: PropTypes.bool,
  /**
   * makes the button completely fluid (full width)
   * @default false
   */
  fluid: PropTypes.bool,
  /**
   * defines the hover effect, can be 'round', 'zoom',  'shrink',  'float', 'reflection'
   * @default undefined
   */
  hover: PropTypes.oneOfType([
    PropTypes.bool,
    PropTypes.oneOf(['round', 'zoom', 'shrink', 'float', 'reflection']),
  ]),
  /**
   * button's action
   * @default (event) => undefined
   */
  onClick: PropTypes.func,
  /**
   * shows an icon, you can pass both the icon name or the instance of an <Icon /> component
   * @default undefined
   */
  icon: PropTypes.oneOfType([
    PropTypes.string,
    PropTypes.instanceOf(Icon),
    PropTypes.arrayOf(PropTypes.string),
  ]),
  /**
   * shows a spinner icon within the button, prop value can be "true" to show a standard <Spinner />
   * or the actual instance of a <Spinner /> component, "false" to not show a spinner.
   * @default false
   */
  spinner: PropTypes.oneOfType([PropTypes.bool, PropTypes.instanceOf(Spinner)]),
  /**
   * @ignore
   */
  children: PropTypes.node,
};

Button.defaultProps = {
  color: 'default',
  size: 'default',
  rounded: false,
  outline: false,
  fluid: false,
  type: 'button',
  disabled: false,
  hover: undefined,
  icon: undefined,
  spinner: false,
  onClick: null,
  children: null,
};

export default React.memo(Button);
